<script>
import axios from "axios";
// import AOS from "aos";
export default {
  name: "mediation",
  data() {
    return {
      // fullName: "",
      firstName: "",
      secondName: "",
      emailAddress: "",
      emailPassword: "",
      loading: false,
      forgotEmail: false,
      resetEmail: "",
    };
  },
  mounted() {
    window.scrollTo(0, 0);
    // AOS.init({
    //   duration: 1000,
    //   offset: 100,
    // });
  },
  methods: {
    resetPwd() {
      this.loading = true;
      axios
        .post(
          process.env.VUE_APP_SERVICE_URL + "ResetPassword",
          {
            email: this.resetEmail,
          },
          { headers: { "Content-Type": "application/json" } }
        )
        .then((res) => {
          console.log(res);
          this.$toasted.success("Email Successfully sended");
          this.loading = false;
          this.forgotEmail = false;
        })
        .catch((Response) => {
          console.log("eerrrooor", Response);
          this.$toasted.error("Something went wrong");
          this.loading = false;
          this.forgotEmail = false;
        });
    },
    handleLogin() {
      if (this.emailAddress === "" || this.emailPassword === "") {
        this.$router.push("/signin");
      } else {
        this.loading = true;
        axios
          .post(
            process.env.VUE_APP_SERVICE_URL + "User/login",
            {
              email: this.emailAddress,
              password: this.emailPassword,
            },
            { headers: { "Content-Type": "application/json" } }
          )
          .then((res) => {
            console.log(res);
            this.$router.push("/profile");
            const userID = res.data.id;
            const userToken = res.data.token;
            localStorage.setItem("UserToken", userToken);
            localStorage.setItem("userID", userID);
            const testUserID = localStorage.getItem("userID");
            console.log("trtttttttttttt", testUserID);
            this.loading = false;
          })
          .catch((Response) => {
            console.log("eerrrooor", Response);
            this.$toasted.error("Invalid Password");
            this.loading = false;
          });
      }
    },
    handleApply() {
      if (this.firstName === "") {
        this.$toasted.error("Invalid First Name");
      } else if (this.secondName === "") {
        this.$toasted.error("Invalid Second Name");
      } else if (this.emailAddress === "") {
        this.$toasted.error("Invalid Email Address");
      } else if (this.emailPassword === "") {
        this.$toasted.error("Invalid Password");
      } else if (!this.emailAddress.match(/@\w{3,20}.\w{2,6}/)) {
        this.$toasted.error("Invalid Email Address");
      } else {
        this.loading = true;
        axios
          .post(
            process.env.VUE_APP_SERVICE_URL + "User/register",
            {
              email: this.emailAddress,
              password: this.emailPassword,
              name: this.firstName,
              lastName: this.secondName,
            },
            { headers: { "Content-Type": "application/json" } }
          )
          .then((res) => {
            console.log(res);
            const userID = res.data.id;
            const userToken = res.data.token;
            localStorage.setItem("userID", userID);
            localStorage.setItem("UserToken", userToken);
            const testuserToken = localStorage.getItem("UserToken");
            console.log("testuserToken", testuserToken);
            this.loading = false;
            this.$router.push("/profile");

            // axios
            //   .post(
            //     "https://api.getresponse.com/v3/contacts",
            //     {
            //       email: this.emailAddress,
            //       name: fullName,
            //       campaign: {
