            //     }
            //   )
            //   .then((res) => {
            //     console.log(res);
            //     this.loading = false;
            //   })
            //   .catch((err) => {
            //     this.loading = false;
            //     console.log(err);
            //   });
          })
          .catch((err) => {
            this.loading = false;
            this.$toasted.error("Already have an account");
            console.log("SignUp", err);
          });
      }
    },
  },
};
